	module.exports = "<h2 class=page-header>Parameters</h2> <pre v-pre><code class=language-javascript>props: {\n\n  /**\n   * Contains the currently selected value. Very similar to a\n   * `value` attribute on an &amp;lt;input&amp;gt;. In most cases, you'll want\n   * to set this as a two-way binding, using :value.sync. However,\n   * this will not work with Vuex, in which case you'll need to use\n   * the onChange callback property.\n   * @type {Object||String||null}\n   */\n  value: {\n    default: null\n  },\n\n  /**\n   * An array of strings or objects to be used as dropdown choices.\n   * If you are using an array of objects, vue-select will look for\n   * a `label` key (ex. [{label: 'This is Foo', value: 'foo'}]). A\n   * custom label key can be set with the `label` prop.\n   * @type {Array}\n   */\n  options: {\n    type: Array,\n    default() { return [] },\n  },\n\n  /**\n   * Enable/disable filtering the options.\n   * @type {Boolean}\n   */\n  searchable: {\n    type: Boolean,\n    default: true\n  },\n\n  /**\n   * Equivalent to the `multiple` attribute on a `&lt;select&gt;` input.\n   * @type {Boolean}\n   */\n  multiple: {\n    type: Boolean,\n    default: false\n  },\n\n  /**\n   * Equivalent to the `placeholder` attribute on an `&lt;input&gt;`.\n   * @type {String}\n   */\n  placeholder: {\n    type: String,\n    default: ''\n  },\n\n  /**\n   * Sets a Vue transition property on the `.dropdown-menu`. vue-select\n   * does not include CSS for transitions, you'll need to add them yourself.\n   * @type {String}\n   */\n  transition: {\n    type: String,\n    default: 'expand'\n  },\n\n  /**\n   * Enables/disables clearing the search text when an option is selected.\n   * @type {Boolean}\n   */\n  clearSearchOnSelect: {\n    type: Boolean,\n    default: true\n  },\n\n  /**\n   * Tells vue-select what key to use when generating option labels when\n   * `option` is an object.\n   * @type {String}\n   */\n  label: {\n    type: String,\n    default: 'label'\n  },\n\n  /**\n   * An optional callback function that is called each time the selected\n   * value(s) change. When integrating with Vuex, use this callback to trigger\n   * an action, rather than using :value.sync to retreive the selected value.\n   * @type {Function}\n   * @default {null}\n   */\n  onChange: Function\n}\n  </code></pre>";

